import React from "react";
import logo from "./logo.svg";
import "./App.css";
// import { start } from "photo2pdf/packages/js-wrapper";
import { start as start2 } from "frontend-photos-to-pdf/packages/js-wrapper";

function App() {
  React.useEffect(() => {
    console.log("START IN MVP");
 
    const onCompleted = (outputs) => {
      alert("sucessfully created pdf, see console and opened pdf");
      console.log(outputs);
      const pdfURL = outputs.pdf;
      window.open(pdfURL, "_blank");
    };
    const onAborted = () => {
      alert("process aborted, closed lib");
    };
    start2({
      process_id: 1,
      document_id: 2,
      filename: "testfile.pdf",
      onCompleted,
      onAborted,
    });
  }, []);

  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
        <a
          className="App-link"
          href="https://reactjs.org"
          target="_blank"
          rel="noopener noreferrer"
        >
          Learn React
        </a>
      </header>
    </div>
  );
}

export default App;
